import { Component } from 'react';
import TodoList from './TodoList';
import initialTodos from '../../data/todos.json';


class AppTodoList extends Component {
    state = {
       todos: initialTodos
    }

    deleteTodo = (todoId) => {
        this.setState(prevState => ({
            todos: prevState.todos.filter(todo => todo.id !==todoId)
        }))
    }

    render () {
        const {todos} = this.state;

        const completedTodos = todos.reduce((acc,todo) => (todo.completed ? acc+1 : acc),0)
        return (
            <> 
            <div>
                <p>Общее количество: {todos.length}</p>
                <p>Количество выполненных: {completedTodos}</p>
            </div>
            <TodoList todos={todos} onDeleteTodo={this.deleteTodo}></TodoList>
            </> 
        )
    }
} 

export default AppTodoList;